<template>
  <div class="menu">
    <a v-for="navlist in menus" :key="navlist">{{navlist}}</a>
  </div>

  <DisCount v-if="showDiscount == true" :disnum="disnum"/>

  <button @click="titleSort()">제목순</button>
  <button @click="priceSort()">낮은가격순</button>
  <button @click="pricehighSort()">높은가격순</button>
  <button @click="priceFilter()">50만원이하</button>
  <button @click="sortBack()">되돌리기</button>

  <CardList :onerooms="onerooms" @openModal="modal=true; active=$event"/>

  <Transition name="fade">
    <MoDal :onerooms="onerooms" :modal="modal" :active="active" @closeModal="modal = false"/>
  </Transition>
  
</template>

<script>
import data from './oneroom.js';
import DisCount from './components/DisCount.vue';
import CardList from './components/CardList.vue';
import MoDal from './components/MoDal.vue';

export default {
  name: 'App',
  data() {
    return {
      oneroomsOrig: [...data],
      onerooms: [...data],
      menus: ['Home', 'Product', 'About'],
      modal: false,
      active: 0,
      showDiscount: true,
      disnum: 30,
    }
  },
  components: {
    DisCount,
    CardList,
    MoDal,
  },
  methods :{
    titleSort(){     
      this.onerooms.sort(function(a,b){
        return a.title.localeCompare(b.title)
      })
    },
    priceSort(){      
      this.onerooms.sort(function(a,b){
        return a.price-b.price
      })
    },
    pricehighSort(){
      this.onerooms.sort(function(a,b){
        return b.price-a.price
      })
    },
    priceFilter(){
      this.onerooms = this.onerooms.filter(v => v.price<500000
      )
    },
    sortBack(){
      this.onerooms = [...this.oneroomsOrig]
      // this.onerooms.sort(function(a,b){
      //   return a.id-b.id
      // })
    }
  },
  mounted(){
    var discount = setInterval(() => {
      this.disnum--;
      if (this.disnum == 0) {
        clearInterval(discount);
      }
    }, 1000);
    
    // setTimeout(()=>{
    //   this.showDiscount = false;
    // }, 2000);
  },

}
</script>

