            <h4 @click="$emit('openModal', item.id)">{{ item.title }}</h4>
            <p>{{ item.price }}</p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'CardList',
    props: {
        onerooms: Object,
    }
}
</script>

<style>
.list {
    width: 100%;
    margin: 20px 0;
    text-align: center;
}
.list img {
    width: 100%;
}
